body {
  font-family: Arial, sans-serif;
  background: #f4f6fa;
  margin: 0;
  padding: 0;
}

.app-container {
  max-width: 800px;
  margin: 40px auto;
  background: #fff;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  padding: 32px;
}

h1,
h2 {
  color: #2c3e50;
}

input,
select,
button {
  font-size: 1rem;
  padding: 8px;
  margin: 4px 0;
  border-radius: 4px;
  border: 1px solid #ccc;
}

table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 16px;
}

th,
td {
  border: 1px solid #e1e4e8;
  padding: 8px;
  text-align: center;
}

th {
  background: #f0f2f5;
}

tr:nth-child(even) {
  background: #f9fafb;
}

button {
  background: #3498db;
  color: #fff;
  border: none;
  cursor: pointer;
  transition: background 0.2s;
}

button:hover {
  background: #217dbb;
}
